html, body {
  margin: 0;
  border: 0;
  font-family: FiraGO;
}

div {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;    
}

#map {
  height: 100vh;
  width: 100vw;
}

.H_logo {
  display: none !important;
}

/* width */
::-webkit-scrollbar {
  width: 4px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #fff;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #54575f;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #0bc7c2;
}